.App {
    font-family: Arial, sans-serif;
    margin: 20px;
}

.header,
section {
    margin-bottom: 20px;
}

.header img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
}

h1,
h2,
h3 {
    color: #d9534f;
}

ul {
    list-style-type: none;
    padding: 0;
}

ul li {
    margin-bottom: 5px;
}

a {
    color: #d9534f;
}